import React, { PureComponent } from 'react';
import './Home.scss';
import { useTranslation, Trans } from 'react-i18next';
import i18n from "i18next";


const { t } = useTranslation();

class Home extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      hasError: false,
      currentTab: '0',
      lang: new URLSearchParams(document.location.search).get("lang") || 'fr',
      products: {
        deleted: [],
        imporved: []
      }
    };
    i18n.changeLanguage(this.state.lang);

    this.getProducts();
  }

  getProducts() {
    fetch(
      `http://vrfraswsesb.adeo.com:5555/ws/productLight/v1/getProduct?bu=1&idProduct=68612943`, {
        withCredentials: true,
        headers: {
          'X-ClientApiKey': 'xnQ65b5le2aIbdyVtVwLDAgM296bsv3f'
        }
      })
      .then(data => data.json())
      .then(res => {
        console.log(res);
        //this.setState({ products });
      })
  }

  goTab = (event) => {
    const goToTab = event.currentTarget.getAttribute('id');
    this.setState({ currentTab: goToTab });
  }

  render() {
    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }
    let cardTemplate = [];
    if (this.state.currentTab === '0') {
      for (let i = 0; i < 3; i++) {
        cardTemplate.push(
          <div className="card" key={i}>
            <div className="image"><img src="https://m1.lmcdn.fr/media/1/5adbc78f1a8f312e825bf3f6/69724053.jpg" alt="" /></div>
            <p>Elagueur à deux mains acier cémenté GEOLIA 73 mm</p>
          </div>);
      }
    } else if (this.state.currentTab === '1') {
      for (let i = 0; i < 3; i++) {
        cardTemplate.push(
          <div className="card" key={i}>
            <div className="image"><img src="https://m1.lmcdn.fr/media/1/5adc5b421a8f312e825c2d9a/70925974.jpg" alt="" /></div>
            <p>Elagueur à deux mains acier cémenté GEOLIA 73 mm</p>
          </div>);
      }
    };

    return (
      <div>
        <h1><Trans>{t('TITLE')}</Trans></h1>
        <p>
          <Trans>{t('TEXT')}</Trans>
        </p>

        <div className={`tabs current-tab--${this.state.currentTab}`}>
          <button id="0" className={(this.state.currentTab === "0") ? 'active' : ''} onClick={this.goTab}><Trans>{t('IMPROVED')}</Trans></button>
          <button id="1" className={(this.state.currentTab === "1") ? 'active' : ''} onClick={this.goTab}><Trans>{t('DELETED')}</Trans></button>
        </div>

        <div className="cards">{cardTemplate}</div>
      </div>
    );
  }
}

export default Home;
